import React, { Component } from 'react'
import './todo-item.css'

export default class TodoItem extends Component{
    constructor(props){
        super(props)
        this.importantHandler = ()=> {
            this.setState((state)=>{
                return {
                    important: !state.important
                }
            })

        }

        this.doneHandler = ()=> {
            this.setState((state)=>{
                return {
                    done: !state.done
                }
            })
        }
        this.state = {
            important: false,
            done: false
        }
    }
    render(){

        const {label, onDeleted} = this.props
        const {done, important} = this.state
        const style = {
            marginLeft: '5px',
            width: '32px'
        }

        let classes = ''
        if(important){
            classes += ' important'
        }
        if(done){
            classes += ' done'
        }
        return(
            <div>
                <span className={classes} onClick={this.doneHandler}>
                    {label}
                </span>
                <button style={style} type='button' onClick={this.importantHandler} className='btn btn-outline-success btn-sm float-right'>
                    <i className="fa fa-exclamation"/>
                </button>
                <button style={style} type='button' className='btn btn-outline-danger btn-sm float-right' onClick={onDeleted}>
                    <i className="fa fa-trash-o"/>
                </button>
            </div>
            )
         }



}
