import React, {Component} from 'react'
import './add-todo.css'


export default class AddTodo extends Component{
    constructor(props){
        super(props)
        this.inputHandler = (e)=>{
            //console.log(e)
            if(e.type === 'click'){
                if(e.target.previousSibling.value === '') return
                console.log(e.target.previousSibling.value)
                e.target.previousSibling.value = ''
            }
            if(e.keyCode === 13){
                if(e.target.value === '') return
                console.log(e.target.value)
                e.target.value = ''
            }

        }
    }

    render() {
        const {onAdded} = this.props
        return(
            <div className="input-group flex-nowrap wd add">
                <input type="text" className="form-control add-input" placeholder="Add Todo" onKeyDown={onAdded}/>
                <button type='button' className='btn btn-outline-success btn-sm float-right' onClick={onAdded}>Add Todo</button>
            </div>
        )
    }
}