import React, { Fragment } from 'react'
import TodoList from "./todo-list/todo-list";
import SearchBar from "./search-bar/search-bar"
import ItemStatusFilter from './item-status-filter/item-status-filter'
import AddTodo from "./add-todo/add-todo";
import Axios from 'axios';


export default  class App extends React.Component {
    constructor(props){
        super(props)
        const initItems = [
            {label: 'Пойти нахуй'},
            {label: 'Нихуя не делать'},
            {label: 'Чё за хуйня'},
            {label: 'Ебать коня в дыхло'},
            {label: 'Ну это последнее'}
        ]
        this.state = {
            items: [...initItems],
            buttonName: "Нажми меня"
        }
        this.deleteItem=(id)=>{
            this.setState((state)=>{
                console.log(id)
                const arr = [...state.items.slice()]
                arr.splice(id, 1)
                return {
                    items: arr
                }
            })

        }
        this.buttonHandler = ()=>{
            console.log("hello")
            Axios.get("http://localhost:8000/users")
        .then(res => {
            this.setState({
                buttonName: res.data.name
              });
        },
          // Примечание: важно обрабатывать ошибки именно здесь, а не в блоке catch(),
          // чтобы не перехватывать исключения из ошибок в самих компонентах.
          (error) => {
              console.log(error)
            this.setState({
              isLoaded: true,
              error
            });
          }
        )
        }
        let prev
        this.searchItem = (e)=>{


            let result = this.state.items.filter((el)=> {
                if(el.label.match(e.target.value) && el.label.match(e.target.value).length > 0 ) return true
                }
            )
            if(e.target.value.length === 0 ) { result = [...new Set(this.state.items), ...new Set(initItems)] }
            if(e.target.value.length < prev) {
                result = [...this.state.items, ...initItems].filter((el)=> {
                    if(el.label.match(e.target.value) && el.label.match(e.target.value).length > 0 ) return true
                })
            }
            prev = e.target.value.length
            this.setState((state)=>{
                return {
                    items: [...new Set(result)]
                }
            })

        }

        this.addItem = (e) =>{
            if(e.type === 'click'){
                if(e.target.previousSibling.value === '') return
                console.log(e.target.previousSibling.value)
                const value = e.target.previousSibling.value
                this.setState((state, e)=>{
                    const arr = [...state.items.slice()]
                    arr.push({label: value})
                    return {
                        items: arr
                    }
                })
                e.target.previousSibling.value = ''
            }
            if(e.keyCode === 13){
                if(e.target.value === '') return
                console.log(e.target.value)
                const value = e.target.value
                this.setState((state)=>{
                    const arr = [...state.items.slice()]
                    arr.push({label: value})
                    return {
                        items: arr
                    }
                })
                e.target.value = ''
            }
        }
    }




    render(){

        return(
            <Fragment>
                
                <div className='top-panel d-flex'>
                    <SearchBar onSearch={ (e)=> this.searchItem(e)}/>
                    <ItemStatusFilter/>
                </div>
                <TodoList todos={this.state.items} onDeleted={(id)=> this.deleteItem(id)}/>
                <AddTodo onAdded={(event)=> this.addItem(event)}/>
            </Fragment>
        )
    }
}
